/* Vars */
:root{
    /* colors */
    --color_brand1: #1585FF;
    --color_brand2: #072123;
    --color_white: #ffffff;
}
/* basics */
html{
    font-size: 10px;
    scroll-behavior: smooth;
  }
body{
    margin: 0;
    position: relative;
  }
*{
    box-sizing: border-box;
  }
h1, h2, h3, h4, h5, h6, p{
    margin: 0;
  }
figure{
    margin: 0;
    padding: 0;
  }
ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
.container{
    padding: 0 10rem;
    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    flex-direction: inherit;
    flex-shrink: inherit;
    width: 100%;
    height: 100%;
  }
/* Animations */
/* Fonts */
@font-face {
    font-family: 'icomoon';
    src:  url('./fonts/icomoon.eot?19mt36');
    src:  url('./fonts/icomoon.eot?19mt36#iefix') format('embedded-opentype'),
      url('./fonts/icomoon.ttf?19mt36') format('truetype'),
      url('./fonts/icomoon.woff?19mt36') format('woff'),
      url('./fonts/icomoon.svg?19mt36#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
.icon-star-full:before {
    content: "\e9d9";
  }
.icon-facebook2:before {
    content: "\ea91";
  }
.icon-instagram:before {
    content: "\ea92";
  }
.icon-twitter:before {
    content: "\ea96";
  }
.icon-youtube:before {
    content: "\ea9d";
  }
/* Header */
.header{
    display: flex;
    width: 100%;
    position: fixed;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(0,0,0,0.5);
    z-index: 9999
}
.header h1{
        margin: .5rem 0;
        color: var(--color_white);
        font-size: 2.4rem;
    }
.nav ul{

        display: flex;
        justify-content: space-between
    }
.nav ul li:not(:last-child){
            margin-right: 1rem;
        }
.nav ul li a{
                color: var(--color_white);
                text-decoration: none;
                font-size: 1.4rem;
            }
/* Hero */
.hero{
    background-image: url(./assets/images/bg-hero2.jpg);
    background-position: center;
    object-fit: cover;
    height: 100vh;
    position: relative
}
.hero .container{
        background: linear-gradient(to right, rgba(0,0,0,.8), rgba(0,0,0,0), rgba(0,0,0,0.8));
    }
.hero .hero_leftside{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 10rem;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column
    }
.hero .hero_leftside h1{
            font-size: 2.8rem;
            color: var(--color_white);
        }
.hero .hero_leftside p{
            font-size: 2.4rem;
            color: var(--color_white);
        }
.hero .hero_background{
        height: 100%;
        width: 100%
    }
.hero .hero_background img:nth-of-type(1){
            position: absolute;
            bottom: 0;
            right: 0;
            left: 0;
            width: 100%;
            transform: translateY(1PX) scaleY(-1);
        }
.hero .hero_background img:nth-of-type(2){
            display: none;
            position: absolute;
            bottom: 0;
            right: 10rem;
            top: 55%;
            width: 30rem;
        }
